.upload {
  padding-top: 20px;
  padding-bottom: 41px;

  @media (min-width: @tablet-width) {
    display: grid;
    grid-template-columns: 1fr 1fr;
    column-gap: 34px;
    padding-top: 57px;
    padding-bottom: 53px;
  }

  @media (min-width: @desktop-width) {
    padding-bottom: 57px;
    background-color: @light-gray;
    column-gap: 0;
  }
}

.upload__image-wrapper {
  grid-row: 1 / 3;

  @media (min-width: @desktop-width) {
    margin-left: 80px;
  }
}

.upload__image {
  display: block;

  @media (min-width: @tablet-width) {
    width: 300px;
    height: 400px;
    object-fit: contain;
  }
}

.upload__editor {
  border: none;
  margin: 0;
  padding: 0;
}

.upload__buttons {
  align-self: end;

  @media (min-width: @tablet-width) {
    margin-left: auto;
    margin-right: auto;
  }

  @media (min-width: @desktop-width) {
    display: flex;
    justify-self: start;
    margin-left: 0;
  }
}

.upload__button {
  width: 280px;
  background-color: transparent;

  &:not(:last-child) {
    margin-bottom: 12px;

    @media (min-width: @tablet-width) {
      margin-bottom: 24px;
    }

    @media (min-width: @desktop-width) {
      margin-bottom: 0;
      margin-right: 44px;
    }
  }

  @media (min-width: @tablet-width) {
    width: 260px;
  }

  @media (min-width: @desktop-width) {
    width: 183px;
  }
}
