.control {
  display: block;
  position: relative;
  padding-left: 59px;
}
.control::before {
  content: "";
  display: block;
  position: absolute;
  top: -4px;
  left: 0;
  width: 39px;
  height: 39px;
  border: 2px solid #e5e5e5;
  box-sizing: border-box;
  background-color: #ffffff;
}
.control--checkbox::before {
  border-radius: 1px;
}
.control--radio::before {
  border-radius: 50%;
}
.control:hover::before {
  border-color: #283645;
}
.control:active::before {
  border-color: #D22856;
}
.control__input--checkbox:checked + .control__label {
  position: relative;
}
.control__input--checkbox:checked + .control__label::before {
  content: "";
  display: block;
  position: absolute;
  z-index: 2;
  width: 39px;
  height: 39px;
  top: -7px;
  left: -58px;
  background: url("../img/icons/checkbox-tick.svg") no-repeat center;
}
.control__input--radio:checked + .control__label {
  position: relative;
}
.control__input--radio:checked + .control__label::before {
  content: "";
  display: block;
  position: absolute;
  z-index: 2;
  width: 15px;
  height: 15px;
  top: 6px;
  left: -58px;
  border-radius: 50%;
  background-color: #D22856;
}
@media (min-width: 660px) {
  .control__input--radio:checked + .control__label::before {
    left: -49px;
  }
}
